.new{
   font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
   font-size: 40px;
   color:rgb(227, 179, 88);
   font-weight: 700;
   margin-left: 10px;
}
#get{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 40px;
    font-weight: bold;
}
#cover{
    background-color: rgb(182, 140, 222);
    width:100%;
  
}
#input{
    display: grid;
    width: 40%;
    margin-left: 30%;
    margin-right: 30%;
    gap:20px
}

#social{
    display:flex;
   margin-left:30%;
   gap:50px;
  padding-bottom:7%;
}
@media screen and (min-width: 320px) and (max-width: 768px){
    #cover{
        width:100%;
        min-height: auto;
    }
    #get{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 40px;
    font-weight: bold; 
    }
    #input{
     display: grid;
     width:60%;
    }
    #social{
        display:grid;
        width: 60%; 
        gap: 30px;
    }

}
@media screen and (min-width: 769px) and (max-width: 1200px){
    #social{
        display:grid;
        width: 80%; 
        gap: 30px;
    }
}
