#projectext{
    font-size: 25px;
    margin-left: 10px;
    font-weight: 400;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-variant: initial; 
}
.name{
    font-size: 40px;
    font-weight: 500;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: rgb(193, 124, 99);
}
#stack{
    font-size: 25px;
    font-weight: 500;
    color: rgb(243, 170, 170);
}
#Tech{
display: flex;
gap:5px;
margin-left: 10px;
}
#modeling{
    width: 100px;
    height: 100px;
}
#intro{
 font-style:oblique;
 font-size: 40px;
 font-weight: 500;
}
#my{
  color:rgb(252, 156, 156);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 40px;
  font-weight: 500;
}
.project{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:20px;
    margin-top: 20px;
}
.card{
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    border-radius: 20px;
    margin-bottom: 100px;
}
img{
    border-radius: 20px;
}
@media screen and (min-width: 320px) and (max-width: 768px){
    .project{
        display: grid;
        gap:20px;
        grid-template-columns: repeat(1,0.5fr);
        margin-top: 20px;
        width: auto;
        margin-left: 30%;
    }
    #Tech{
        width: 60%;
        display: flex;
       
    }
    #pro{
        display: grid;
        grid-template-rows: repeat(1,1fr);
        grid-template-columns: repeat(1,1fr) ;
        gap:20px;
        width: 100%;
    }
    #modeling{
      width:35%;
      height: 35%;
    }
    #projectext{
        font-size: 16px;
        margin-left: 10px;
        font-weight: 400;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-variant: initial;
       
    }
    .arrange{
        display: flex;
        width: 70px;
        gap:20px;
    }
}
@media screen and (min-width: 769px) and (max-width: 1200px){
    .project{
        display: grid;
        gap:20px;
        grid-template-columns: repeat(1,0.5fr);
        margin-top: 20px;
        width: auto;
        margin-left: 30%;
    }
    #Tech{
        width: 60%;
        display: flex;
       
    }
    #modeling{
        width:35%;
        height: 35%;
      }
    #pro{
        display: grid;
        grid-template-rows: repeat(1,1fr);
        grid-template-columns: repeat(1,1fr) ;
        gap:20px;
        width: 100%;
    }
    .project{
        display: grid;
        gap:20px;
        grid-template-rows: repeat(2,0.5fr);
        margin-top: 20px;
        width: 60%;
    }
    #projectext{
        font-size: 16px;
        margin-left: 10px;
        font-weight: 400;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-variant: initial;
       
    }
    .arrange{
        display: flex;
        width: 70px;
        gap:20px;
    }
}